*{
padding:0;
margin:0;
box-sizing: border-box;

}

.menuContainer{

display: grid;
place-items: center;
height: 40vh;

}
.menuBox img{
 width: 300px;
 height: 300px;
 object-fit: cover;
 box-shadow: 0 0 5px grey;
 border-radius: 10px;
display: block;
margin: auto

}

.menuBox{
  background-color: beige;
  width: 400px;
  height: auto;
  box-shadow: 0 0 5px gray;
  padding: 10px 0;
  border-radius: 10px;
  outline: green;
  outline-width: 5px;
  outline-style: dashed;
}

h1{
  text-align: center;
  font-size: x-large;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
h2{
text-align: center;
font-size: large;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
p{
  width:95%;
  margin:10px auto;
  font-size: medium;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.btn{
text-align: center;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

}

button{
  background-color: transparent;
  border: none;
  font-size: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: black;
  background-color: darksalmon;
  outline: green;
  outline-style: dotted;
}

#random{
  box-shadow: 0 0 5px gray;
  margin: 0 5px;
  padding: 0 5px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

